import { transformResponse } from ".";
import {
  IServerResponse,
  IDirectoryResponseSuccess,
  IGetPathArgs,
  IPostPathArgs
} from "@/shared";
import { axiosInstance } from "./axios";

export const getDirectoryContents = async (
  args: IGetPathArgs
): Promise<IServerResponse<IDirectoryResponseSuccess>> => {
  console.log("args", args);
  const response = await axiosInstance.get<
    IServerResponse<IDirectoryResponseSuccess>
  >(`/system/directory`, { params: args });
  return transformResponse(response);
};

export const getDirectoryContentsOnClient = async (args: IGetPathArgs) => {
  console.log("called client function");
};

export const createDirectory = async (
  args: IPostPathArgs
): Promise<IServerResponse<{}>> => {
  const response = await axiosInstance.post<IServerResponse<{}>>(
    `/system/directory`,
    args
  );
  return transformResponse(response);
};
